          <!-- <div class="user-box">
            <div></div>
            头像
            <div class="user-avatar"></div>
          </div> -->

          <div class="item">
            <van-grid :column-num="column">
              <van-grid-item icon="coupon-o" text="电影票" />
              <van-grid-item @click="viewLikeMovie" icon="like-o" text="我的收藏" />
            </van-grid>
          </div>

          <div class="myserve">
            <div class="serve">我的服务</div>
            <div class="mycard">
              <img src="../../assets/images/折扣卡.png" alt />
              <div class="card">折扣卡</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/优惠券.png" alt />
              <div class="card">优惠券</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/专享卡.png" alt />
              <div class="card">猫享卡</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/银行卡.png" alt />
              <div class="card">银行活动</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/购物袋.png" alt />
              <div class="card">周边商城</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/电音之旅.png" alt />
              <div class="card">电音之旅</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/0元看电影.png" alt />
              <div class="card">0元看电影</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/明星应援.png" alt />
              <div class="card">明星应援</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/在线视频.png" alt />
              <div class="card">在线视频</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/天天领现金.png" alt />
              <div class="card">天天领现金</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/认证.png" alt />
              <div class="card">J认证</div>
            </div>
            <div class="mycard">
              <img src="../../assets/images/借.png" alt />
              <div class="card">分期/借钱</div>
            </div>
          </div>

          <div>
            <van-cell title="个人信息" icon="contact">
              <!-- 使用 right-icon 插槽来自定义右侧图标 -->
              <van-icon slot="right-icon" name="arrow" style="line-height: inherit;" />
            </van-cell>
            <van-cell title="设置" icon="setting-o">
              <!-- 使用 right-icon 插槽来自定义右侧图标 -->
              <van-icon slot="right-icon" name="arrow" style="line-height: inherit;" />
            </van-cell>
          </div>
        </div>
      </div>
    </div>
  </van-pull-refresh>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState } = createNamespacedHelpers("mineModule");

export default {
  name: "mine",
  created() {
    // console.log("mine created");
  },

  data() {
    return {
      column: 2,
      isLoading: false
    };
  },

  computed: {
    ...mapState(["userInfo"])
  },

  methods: {
    //退出或者登录
    login() {
      let userStatus = localStorage.getItem("user");

      if (userStatus) {
        let userInfo = JSON.parse(userStatus);

        if (userInfo.isLogin) {
          // console.log("清空登录状态");

          localStorage.removeItem("user");

          this.$store.commit("mineModule/changeUserInfo", {
            loginText: "登录",
            username: ""
          });
        } else {
          this.$router.push({ name: "login" });
        }
      } else {
        this.$router.push({ name: "login" });
      }
    },

    //查看我的收藏电影
    viewLikeMovie() {
      //验证是否登录
      let userStatus = localStorage.getItem("user");

      if (userStatus) {
        let userInfo = JSON.parse(userStatus);

        //如果登录
        if (userInfo.isLogin) {
          // console.log("查看我的收藏电影");

          this.$router.push({ name: "likeMovie" });
        } else {
          //如果未登录
          this.$toast("请先登录");
        }
      } else {
        //未登录
        this.$toast("请先登录");
      }
    },

    onRefresh() {
      setTimeout(() => {
        this.$toast("刷新成功");
        this.isLoading = false;
        this.count++;
      }, 500);
    }
  },

  created() {
    // console.log("mine created");

    //获取用户信息
    let userStatus = localStorage.getItem("user");

    if (userStatus) {
      let user = JSON.parse(userStatus);

      if (user.isLogin) {
        this.$store.commit("mineModule/changeUserInfo", {
          loginText: "退出",
          username: user.username
        });
      }
    }
  }
};
</script>

