      top: -50px;
    }
    .user-boxing {
      position: absolute;
      left: 0;
      top: -10px;
      width: 100%;
      height: 80px;
      background-color: #ed3743;
      z-index: 2;
      .user-logo {
        position: absolute;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        left: 40px;
        top: -40px;
        overflow: hidden;
        box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.3);
      }
    }
    .user-box {
      height: 80px;
      background-color: #fff;
      position: relative;
      z-index: 1;
      .user-avatar {
        position: absolute;
        left: 40px;
